"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import api from "@/lib/axios";
import {
  Store,
  Phone,
  MapPin,
  Image,
  Utensils,
  Globe,
  FileText,
} from "lucide-react";

export default function AddRestaurantPage() {
  const router = useRouter();

  const [form, setForm] = useState({
    managerId: "",
    name: "",
    phone: "",
    description: "",
    location: "",
    imageUrl: "",
    cuisineType: "",
    country: "IN",
  });
  const [success, setSuccess] = useState("");

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>
  ) => {
    setForm({...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError("");

    try {
      await api.post("/restaurants/manager", form);
      setSuccess("Restaurant created successfully! Redirecting to dashboard...");
      setTimeout(() => {
      router.push("/dashboard/manager");
    }, 1500);
    } catch (err: any) {
      setError(err?.response?.data?.message || "Failed to create restaurant");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-orange-50 via-white to-yellow-50 py-12 px-4">
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className="mb-10">
          <h1 className="text-4xl font-extrabold text-orange-700">
            Add New Restaurant
          </h1>
          <p className="text-gray-600 mt-2 text-lg">
            Fill in your restaurant details to get started
          </p>
        </div>

        {/* Error */}
        {error && (
          <div className="bg-red-100 border border-red-300 text-red-700 p-4 rounded-xl mb-6">
            {error}
          </div>
        )}

        {/* Form */}
        <form
          onSubmit={handleSubmit}
          className="bg-white shadow-xl border border-orange-100 rounded-3xl p-8 space-y-8"
        >
            
          {/* Basic Info */}
          <div>
            <h2 className="text-2xl font-bold text-orange-600 mb-4 flex items-center gap-2">
              <Store /> Basic Info
            </h2>

            <div className="grid md:grid-cols-2 gap-6">
              <input
                name="name"
                placeholder="Restaurant Name"
                value={form.name}
                onChange={handleChange}
                required
                className="border p-3 rounded-xl text-black placeholder-gray-400 focus:ring-2 focus:ring-orange-400"
              />

              <input
                name="phone"
                placeholder="Phone Number"
                value={form.phone}
                onChange={handleChange}
                required
                className="border p-3 rounded-xl text-black placeholder-gray-400 focus:ring-2 focus:ring-orange-400"
              />
            </div>
          </div>

          {/* Description */}
          <div>
            <label className="flex items-center gap-2 text-orange-600 font-semibold mb-2">
              <FileText /> Description
            </label>
            <textarea
              name="description"
              rows={3}
              placeholder="Short description about your restaurant"
              value={form.description}
              onChange={handleChange}
              className="w-full border p-3 rounded-xl text-black placeholder-gray-400 focus:ring-2 focus:ring-orange-400"
            />
          </div>

          {/* Location */}
          <div>
            <label className="flex items-center gap-2 text-orange-600 font-semibold mb-2">
              <MapPin /> Location
            </label>
            <input
              name="location"
              placeholder="City, Area"
              value={form.location}
              onChange={handleChange}
              required
              className="w-full border p-3 rounded-xl text-black placeholder-gray-400 focus:ring-2 focus:ring-orange-400"
            />
          </div>

          {/* Media & Cuisine */}
          <div className="grid md:grid-cols-2 gap-6">
            <input
              name="imageUrl"
              placeholder="Image URL"
              value={form.imageUrl}
              onChange={handleChange}
              className="border p-3 rounded-xl text-black placeholder-gray-400 focus:ring-2 focus:ring-orange-400"
            />

            <input
              name="cuisineType"
              placeholder="Cuisine Type (Indian, Italian, etc.)"
              value={form.cuisineType}
              onChange={handleChange}
              className="border p-3 rounded-xl text-black placeholder-gray-400 focus:ring-2 focus:ring-orange-400"
            />
          </div>

          {/* Country */}
          <div>
            <label className="flex items-center gap-2 text-orange-600 font-semibold mb-2">
              <Globe /> Country
            </label>
            <select
              name="country"
              value={form.country}
              onChange={handleChange}
              className="w-full border p-3 rounded-xl text-black focus:ring-2 focus:ring-orange-400"
            >
              <option value="IN">India</option>
              <option value="US">United States</option>
            </select>
          </div>

          {/* Submit */}
          <button
            type="submit"
            disabled={loading}
            className="w-full bg-gradient-to-r from-orange-500 to-orange-600 text-white py-4 rounded-2xl text-lg font-bold hover:opacity-90 transition disabled:opacity-60"
          >
            {loading ? "Creating Restaurant..." : "Create Restaurant"}
          </button>
          {success && (
  <div className="mb-6 bg-green-100 border border-green-300 text-green-700 p-4 rounded-xl font-semibold">
    {success}
  </div>
)}
        </form>
      </div>
    </div>
  );
}
